import React from 'react'
import './about.css'
function About() {
  return (
    <div className="modal fade" id="exampleModal" tabIndex="-1">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-body about">
            <div className="writing">
              <p>
                This is official website of Ambala Cantt. based firm called Archiriti Architects.
                It was established and serving since 1998. Archiriti architects owner name is
                Ar. Jagdish chawla of ambala cantt.Jagdish chawla is held post of Chairman of 
                Architects and Engineers Association Distt. Ambala from 2020-2022. It also offers
                  3ds elevations, evaluations,
                with construction projects etc. We handle residential as well as commercial
                projects from small houses to big villas or small temples to big industries.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About