import React, { useState,useEffect } from 'react'
import { Link } from 'react-router-dom';
import './header.css';
function Header() {
  function nav(){
    let nav = document.querySelector('.navigation-bar');
    if( window.pageYOffset!==0){
      nav.classList.add('box-shadow');
    }else{
      nav.classList.remove('box-shadow');
    }
  }
  window.addEventListener('scroll',nav);
  
    const [showTabsMenu, setshowTabsMenu] = useState(false)
    function togMenu(){
        if(showTabsMenu){
          setshowTabsMenu(false);
        }else{
          setshowTabsMenu(true);
        }
      }
  return (
    <>
    
    <nav className="navigation-bar sticky-top">
        {/* <!-- this div contains the logo and title os the page --> */}
        <div className="title-combo">
          <p><Link href="/">Archiriti Architects</Link></p>
        </div>
        <div className="tabs d-none d-md-flex">
        {/* <!-- {/* this is for notification bell --> */}
            <div className="section rounded">
              <Link to="/">
                Home
              </Link>
            </div>
            <div className="section rounded">
              <a href="#about" data-bs-toggle="modal" data-bs-target="#exampleModal">
                  About
              </a>
            </div>
            <div className="section rounded">
              <Link to="/projects" >
                Projects
              </Link>
            </div>
            <div className="section rounded">
              <Link to="/contactus">
                  Contact us
              </Link>
            </div>
            {/* <!-- <div className="section rounded d-inline-block"></div> --> */}
        </div> 
        {/* <div className={"tabs-menu-btn d-block d-md-none"}>
            <a  role="button" onClick={togMenu} id="toggle-menu"><i className={"fas d-flex fa-bars"}></i></a>
        </div> */}
    </nav>
<div className={`tabs-menu d-${showTabsMenu?'block':'none'}`}>
  <div className="tabs-list">
    <ul type="NONE" id="set">
      <button type="button" onClick={togMenu} className="btn-close btn-colour position-absolute mt-1 end-0 mr-5"  style={{"fontSize":"30px"}} aria-label="Close"></button>
      <br/>
      <br/>
      <li><Link to="/">Home</Link></li>
      <hr/>
      <li><a href="#about" >About</a></li>				
      <hr/>
      <li><Link to="/projects">Projects</Link></li>
      <hr/>
      <li><Link to="/contactus">Contacts</Link></li>
      <hr/>
    </ul>
  </div>
</div>
</>

  )
}

export default Header;