 
 import React,{useEffect,useState} from 'react';
import './Intro.css';
function Intro() {
  const [data,setData] = useState('')
  const [quote, setQuote] = useState({});
  useEffect(() => {
    const Convey = async()=>{
      let resp =  await fetch('https://archiritiarchitects.herokuapp.com/quotes');
      let respJson = await resp.json();
      const num = Math.floor(Math.random()*10);
      setQuote(respJson[num])
    }
    if(document.readyState==='complete'){
      Convey();
    }else{
      window.addEventListener('load',Convey)
      return () => window.removeEventListener('load', Convey);
    }
  }, [])
  return (
    <div id="Home" className="Intro-box  ">
        {/* <!-- qoute Line --> */}
        <div className='quote'><p >{quote.content}<br></br><span style={{"color":" rgb(231, 123, 84)"}} >~ {quote.author}</span></p></div>
        <img src="../../../../assets/media/introPic/intro.webp"></img>
        {/* <!-- social button --> */}
        <span className="d-none hello d-md-inline">Follow us on : </span><a href="https://www.facebook.com/archiritiarchitects" target="_blank" ><i id="fb" className="fab social fa-facebook"></i></a>
    </div>
  )
}

export default Intro